import React, { useState } from 'react'
import { ArticleInfo } from '../models/ArticleInfo';

interface AddCommentFormProps {
    articleName: string;
    setArticleInfo: (articleInfo: ArticleInfo) => void;
}

export const AddCommentForm = (props: AddCommentFormProps) => {
    const { articleName, setArticleInfo } = props;

    const [username, setUsername] = useState<string>('');
    const [commentText, setCommentText] = useState<string>('');

    const addComment = async () => {
        const result = await fetch(`/api/articles/${articleName}/add-comment`, {
            method: 'post',
            body: JSON.stringify({ username, text: commentText }),
            headers: {
                'Content-Type': 'application/json',
            }
        });
        const body: ArticleInfo = await result.json();
        setArticleInfo(body);
        setUsername('');
        setCommentText('');
    }

    return (
        <div id="add-comment-form">
            <h3>Add a Comment</h3>
            <label>
                Name: <input type="text" value={username} onChange={(event) => setUsername(event.target.value)} />
            </label>
            <label>
                Comment: <textarea rows={4} cols={50} value={commentText} onChange={(event) => setCommentText(event.target.value)} />
            </label>
            <button onClick={addComment}>Add Comment</button>
        </div>
    )
}
