import React from 'react'
import { Comment } from '../models/Comment'

interface CommentListProps {
    comments: Comment[];
}

export const CommentsList = (props: CommentListProps) => {
    return (
        <>
            <h3>Comments:</h3>
            {props.comments.map((comment: Comment, key: number) => (
                <div className="comment" key={key}>
                    <h4>{comment.username}</h4>
                    <p>{comment.text}</p>
                </div>
            ))}
        </>
    )
}
