import React, { useState, useEffect } from 'react';
import { articles } from './article-content';
import { useParams } from 'react-router-dom';
import { ArticlesList } from '../components/ArticlesList';
import { NotFoundPage } from './NotFoundPage';
import { ArticleInfo } from '../models/ArticleInfo';
import { CommentsList } from '../components/CommentsList';
import { UpvotesSection } from '../components/UpvotesSection';
import { AddCommentForm } from '../components/AddCommentForm';

export const ArticlePage = () => {
    const { name } = useParams();
    const article = articles.find(article => article.name === name);

    const [articleInfo, setArticleInfo] = useState<ArticleInfo>();

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body: ArticleInfo = await result.json();
            console.log(body);
            setArticleInfo(body);
        }
        fetchData();
    }, [name])

    if (!article) return <NotFoundPage />

    const otherArticles = articles.filter(article => article.name !== name);

    return (
        <>
            <h1>{article.title}</h1>
            <UpvotesSection articleName={name} upvotes={articleInfo?.upvotes ?? 0} setArticleInfo={setArticleInfo} />
            {article.content.map((paragraph, key) => (
                <p key={key}>{paragraph}</p>
            ))}
            {articleInfo && <CommentsList comments={articleInfo.comments} />}
            <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
            <h3>Other Articles:</h3>
            <ArticlesList articles={otherArticles} />
        </>
    );
}
