import React from 'react'
import { ArticleInfo } from '../models/ArticleInfo';

interface UpvotesSectionProps {
    articleName: string;
    upvotes: number;
    setArticleInfo: (articleInfo: ArticleInfo) => void;
}

export const UpvotesSection = (props: UpvotesSectionProps) => {
    const {articleName, upvotes, setArticleInfo} = props;

    const upvoteArticle = async () => {
        const result = await fetch(`/api/articles/${articleName}/upvote`, {
            method: 'post',
        });
        const body: ArticleInfo = await result.json();

        setArticleInfo(body);
    }

    return (
        <div id="upvotes-section">
            <button onClick={upvoteArticle}>Add Upvote</button>
            <p>This post has been upvoted {upvotes} times</p>
        </div>
    )
}
